.container__ram{
    display: flex;
    justify-content: space-between;
}

.container__filter{
 width: 30%;
}

.characters-items{
    width: 70%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.select{
    margin: 20px 0;
}

button {
    margin-bottom: 10px;
    margin-right: 10px;
}
.characters-item{
  width: 230px;
  cursor: pointer;
  transition: all 0.7s;
}
.characters-item:hover{
    transform: scale(1.1);
  }

.characters-item img{
    width: 230px;
  }
.pagination{
    margin-bottom: 20px !important;
}

 h4{
    margin: 0.52rem 0 0.912rem 0
 }
.characters-item p span{
    font-size: 19px;
    
}